.resumeTwo{
    width: 100%;
    margin: 20px auto;
    background-color: white;
    display: flex;
    align-items: center;
    padding: 15px;
}
.resumeTwo .content{
    display: flex;
    width: inherit;
    min-width: 1050px;
    max-width: 1050px;
    min-height: 1250px;
    max-height: 1250px;
}
.resumeTwo .left-area{
    min-width: 35%;
    max-width: 35%;
    padding: 10px;
}
.resumeTwo .left-area .personal-info{
    margin: 10px;
}
.resumeTwo .personal-info>div{
    -webkit-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    border-radius: 5px;
    margin-top: 10px;
    padding: 10px 25px;

}
.resumeTwo .left-area .profile{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    padding: 20px 40px;
    text-align: center;
    word-break: break-all;

}
.resumeTwo .left-area .profile img{
    border-radius: 50%;
    width: 200px;
    height: 200px;
}
.resumeTwo .left-area .profile h4{
    color: #1e1950;
    margin: 2px auto;
    margin-top: 15px;

}
.resumeTwo .left-area .profile h5{    
    color: #ec642a;
    margin: 2px auto;
    padding-bottom: 15px;
    border-bottom: 1px solid #1e1950;
}
/* .resumeTwo .left-area .profile p{
    text-align: center;
    font-weight: 500;
    font-size: medium;
} */
.resumeTwo .personal-info .work{
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: none;
    padding: 10px 0px;
    padding-bottom: 20px;
    flex-wrap: wrap;
}
.resumeTwo .personal-info .work>div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    padding: 10px 15px;
    color: white;
    margin-top: 10px;
    max-width: 110px;
    min-width: 100px;
    max-height: 67px;
    white-space: nowrap;
}
.resumeTwo .personal-info .work .projects{
    background-color: #1e1950;
}
.resumeTwo .personal-info .work .customers{
    background-color: #ec642a;
}
.resumeTwo .personal-info .work .experience{
    background-color: #b59c7a;
}
.resumeTwo .personal-info .work h4{
    font-size: 18px;
    color: white;
}
.resumeTwo .personal-info .work p{
    font-size: 12px;
    color: white;
}
.resumeTwo .contact{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    padding: 5px;
}
.resumeTwo .left-area .contact .inner-div{
    width: 80%;
}
.resumeTwo .left-area .contact .inner-div>div{
    display: flex;
    align-items: center;
    justify-content: left;
    margin: 5px;
}
.resumeTwo .left-area .contact .inner-div img{
    margin-right: 20px;
}
