.resumeTwo .left-area .goal{
    margin: 10px;
}
.resumeTwo .left-area .goal>div{
    -webkit-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    border-radius: 5px;
    margin-top: 10px;
    padding: 10px 25px;
    background-color: white;
}
.resumeTwo .left-area .goal .graph{
    height: 250px;
    padding: 50px 0;
    position: relative;
}
.resumeTwo .left-area .goal .point{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: none;
}
.resumeTwo .left-area .goal .old{
    position: absolute;
    left: 21px;
    top: 120px;
    background-color: black;

}
.resumeTwo .left-area .goal .current{
    position: absolute;
    left: 170px;
    top: 103px;
    background-color: #1e1950;
}
.resumeTwo .left-area .goal .next{
    position: absolute;
    right: 15px;
    top: 58px;
    background-color: #ec642a;
}
.resumeTwo .left-area .goal .job{
    position: absolute;
    font-weight: 500;
    color: #454162;
}
.resumeTwo .left-area .goal .old-job{
    top: 144px;
    left: 23px;
}
.resumeTwo .left-area .goal .current-job{
    top: 59px;
    left: 106px;
    background-color: #1e1950;
    color: white;
    font-weight: 600;
    padding: 5px 10px;

}
.resumeTwo .left-area .goal .current-job span{
    height: 10px;
    width: 10px;
    position: absolute;
    top: 23px;
    transform: rotate(45deg);
    background-color: #1e1950;
    left: 63px;
}
.resumeTwo .left-area .goal .next-job{
    top: 31px;
    right: 11px;
    white-space: nowrap;
    color: #ec642a;
}
.resumeTwo .left-area .goal .graph img{
    width: 100%;
    height: 80%;
}
.resumeTwo .right-area{
    max-width: 65%;
    min-width: 65%;
    padding: 10px;
}
.resumeTwo .right-area .experience{
    margin: 10px;
}
.resumeTwo .right-area .experience>div{
    -webkit-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    border-radius: 5px;
}
.resumeTwo .right-area .experience .work-experience{
    display: flex;
    align-items: flex-start;
    justify-content: right;
    background-color: white;
    padding: 25px 55px;
    margin-top: 10px;
    max-height: 400px;
    max-width: 638px;
    word-break: break-all;
}
.resumeTwo .right-area .experience .left{
    min-width: 120px;

}
.resumeTwo .right-area .left div{
    min-height: 100px;
    margin-bottom: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    margin-right: 5px;

}
.resumeTwo .right-area .left span{
    border: 1.5px solid #454162;
    white-space: pre-wrap;
    border-radius: 50%;
    width: 17px;
    height: 17px;
    color: white;
}
.resumeTwo .right-area .right>div{
    min-height: 100px;
    margin-bottom: 10px;
}
.resumeTwo .right-area .right h4{
    color: #1e1950;
    font-weight: 600;
}
.resumeTwo .right-area .right>div div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
    font-size: 14px;
}
.resumeTwo .right-area .right>div div span{
    border-radius: 5px;
    padding: 5px 10px;
    display: inline-block;
    white-space: nowrap;
    float: right;
}
.resumeTwo .right-area .experience .right>div div span.current{
    color: #ec642a;
    background-color: #f44f1d2f;
}
.resumeTwo .right-area .experience .right p{
    margin-bottom: 5px;

}
.resumeTwo .right-area .education{
