}
.resumeTwo .right-area .education .left{
    min-width: 100px;

}
.resumeTwo .right-area .education .left div{
    min-height: 100px;
    margin-bottom: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
