    color: white;
}

.resumeTwo .right-area .right>div div{
    margin-bottom: 5px;
}
.resumeTwo .right-area .right>div div span{
    font-weight: 600;
    background-color: #422fbd70;
    border-radius: 5px;
    padding: 5px 10px;
