.resumeTwo .skills{
    margin: 10px;
}
.resumeTwo .skills>div{
    -webkit-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    border-radius: 5px;
}
.resumeTwo .skills .software-skills{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 25px 55px;
    margin-top: 10px;
    flex-wrap: wrap

}
.resumeTwo .software-skills>div{
    width: 45%;
    margin: 5px 10px;
}
.resumeTwo .software-skills p{
    font-weight: 500;
}  
.resumeTwo progress {
  border-radius: 10px; 
  width: 80%;
  height: 5px;
  background-color: #422fbd70;
}
.resumeTwo progress::-webkit-progress-bar {
  border-radius: 10px;
}
.resumeTwo progress::-webkit-progress-value {
  background-color: #1e1950;
  border-radius: 10px;
}
.resumeTwo progress::-webkit-progress-value::after {
  background-color: black;
  border-radius: 10px;
  content: "dfghj";
}
.resumeTwo .right-area .interests{
    margin: 10px;
}
.resumeTwo .right-area .interests>div{
    -webkit-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
    box-shadow: -1px -1px 45px -11px rgba(0,0,0,0.72);
