/* Alert */
.loading{
    display: block !important;
}
.success{
    color: #20a020;
}
.error{
    color: #a02020;
}
.alert{
    padding: 1rem;
    border: 0.1rem solid transparent;
    border-radius: 0.5rem;
}
.alert-info{
    color: #2020a0;
    background-color: #e0e0ff;
}
.alert-danger{
    color: #a02020;
    background-color: #ffe0e0e0;
}