.headingsColumn {
  padding-top: 10%;
}

.img-dv {
  position: relative;
  width: 100%;
  z-index: 1;
}

.img-dv:hover .view-template {
  opacity: 1;
}

.img-dv:hover {
  filter: grayscale(5);
}

.LinkHover:hover {
  text-decoration: none;
}

.view-template {
  opacity: 0;
  width: 130px;
  height: 40px;
  border-radius: 5px;
  color: #0a2c66;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  font-style: AvenirText;
  z-index: 100;
  box-shadow: 0 5px 15px rgba(145, 92, 182, 0.4);
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.LoginButtons {
  font-size: 10px;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 4px;
  width: 100%;
}

.dropDown {
  display: flex;
  justify-content: center;
  box-shadow: 0px 0px 8px 1px rgba(209, 209, 209, 0.76);
  border-radius: 5;
  padding-top: 2.5%;
  max-width: 100vw;
  /* align-items: center; */
}

.listItems::before {
  color: #ececec;
  font-weight: bold;
  display: inline-block;
  width: 3em;
  margin-left: -1em;
  opacity: 0.5;
}

.feedbackBar {
  width: 90%;
  padding: 5px;
  font-family: AvenirText;
  font-size: 12px;
  border-radius: 5px;
  box-shadow: 0px 0px 6px -4px rgba(0, 0, 0, 1);
}

.AllCvHeading {
  font-size: 30px;
  font-family: RobotoHeadingMedium;
  font-weight: 500;
}

.templeateHeadingInfo {
  font-family: AvenirTextBlack;
  font-size: 15px;
}

form .btn {
  background-color: #0a2c66;
  color: white !important;
  font-size: 12px;
  margin-left: -2%;
  font-family: RobotoHeading;
}

@media only screen and (min-width: 900px) {
  .headingsColumn {
    text-align: left;
    padding-left: 8%;
    padding-right: 5%;
  }

  .buttonSpacing {
    margin-left: 3%;
  }

  .professional {
    color: #ff4309;
    font-size: 2em;
    font-family: RobotoHeading;
  }

  .mainHeading {
    color: #ffffff;
    font-size: 3vw;
    font-family: RobotoHeading;
  }

  .secondMainHeading {
    color: #ffffff;
    font-size: 2.5vw;
    font-family: RobotoHeading;
  }

  .TitlePara {
    font-size: 0.9vw;
    margin-top: 10%;
    color: #ffffff;
    font-family: AvenirText;
  }

  .cvTitle {
    font-size: 13px;
    font-family: RobotoHeading;
    font-weight: bolder !important;
    margin-top: 50px;
  }

  .cvText {
    font-size: 12px;
    font-family: AvenirText;
    margin-top: 20px;
  }

  .CreateCVTextHeading {
    font-family: RobotoHeadingMedium;
    font-size: 30px;
    color: #ffffff !important;
  }

  .listItems {
    font-family: AvenirText;
    font-size: 10px;
    color: #ffffff;
    line-height: 25px;
  }

  .title {
    background-image: url("./../../Assets/images/Path50309.png");
    background-size: contain;
    background-repeat: no-repeat;
  }
}

@media only screen and (max-width: 900px) {
  .headingsColumn {
    text-align: center;
  }

  .buttonSpacing {
    margin-left: 0% !important;
  }

  .professional {
    color: #ff4309;
    font-size: 1.8em;
  }

  .mainHeading {
    color: #ffffff;
    font-size: 2em;
    font-family: RobotoHeading;
  }

  .secondMainHeading {
    color: #ffffff;
    font-size: 1.5em;
    font-family: RobotoHeading;
  }

  .TitlePara {
    font-size: 0.9em;
    margin-top: 10%;
    color: #ffffff;
    font-family: AvenirText;
  }

  .title {
    background-image: url("./../../Assets/images/Path50309.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-size: 1000px;
  }

  .cvTitle {
    font-size: 13px;
    font-family: RobotoHeading;
    font-weight: bolder !important;
    margin-top: 50px;
  }

  .cvText {
    font-size: 12px;
    font-family: AvenirText;
    margin-top: 20px;
  }

  .CreateCVTextHeading {
    font-family: RobotoHeadingMedium;
    font-size: 30px;
    color: #ffffff !important;
  }

  .listItems {
    font-family: AvenirText;
    font-size: 10px;
    color: #ffffff;
    line-height: 25px;
  }
}

@media (min-width: 1200px) {

  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 72% !important;
  }
}

