    .resume15   progress {
        color: #E05780;;
        width: 200pt;
        height: 8px;
        margin-bottom: 12px;
    }
    
    
    .resume15   progress::-moz-progress-bar {
        background: #E05780;;
        border: solid 3pt #E05780;;
        width: 150pt;
    }
    
    .resume15  progress::-webkit-progress-value {
        background: #E05780;;
        border: solid 3pt #E05780;;
        width: 150pt;
    }
    
    .resume15   progress[aria-valuenow]:before {
