.resume14   progress {
    color: #FFE32C;
    width: 130pt;
    height: 5px;
    margin-bottom: 12px;
}


.resume14   progress::-moz-progress-bar {
    background: #FFE32C;
    border: solid 2pt #FFE32C;
    width: 130pt;
}

.resume14  progress::-webkit-progress-value {
    background: #FFE32C;
    border: solid 2pt #FFE32C;
    width: 100pt;
}

.resume14   progress[aria-valuenow]:before {
    background :#FFE32C;
    border: solid 1pt #FFE32C;
    width: 100pt;
}


.resume14    progress {
    -webkit-appearance: none;
    appearance: none;
} 

