.resume5 progress {
    color: #363636;
    width: 150pt;
    height: 3pt;
}
.resume5  progress::-moz-progress-bar {
    background:#363636;;
    border: solid 1pt #363636;;
    width: 150pt;
    height: 4px;
}
progress::-webkit-progress-value {
    background: #363636;;
    border: solid 1pt #363636;;
    width: 150pt;
    height: 3px;
}
.resume5 progress[aria-valuenow]:before {
    background:#363636;;
    border: solid 0pt#363636;;
    width: 150pt;
    height: 2px;
}
.resume5 progress {
    -webkit-appearance: none;
    appearance: none;
}
